
.comment-card-wrap{
	background-color: #fff;
	padding-left: 8px;
	padding-right: 8px;
	padding-bottom: 8px;
}
.comment-card-part{
	border-top: 1px dashed #eee;
}
.comment-card-all{
	margin-bottom: 8px;
}
.comment-card-wrap .head{
	height: 40px;
	line-height: 40px;
	font-size: 15px;
	color: #404040;
}
.comment-card-wrap .head img{
	width: 28px;
	height: 28px;
	padding-right: 4px;
	vertical-align: middle;
}
.comment-card-wrap .head .right-tip{
	float: right;
	font-size: 12px;
	color: #868686;
}
.comment-card-wrap .body{
}
.comment-card-wrap .body .comment-score{
	font-size: 14px;
	line-height: 1.4;
	color: #404040;
	padding-bottom: 4px;
}
.comment-card-wrap .body .comment-text{
	font-size: 14px;
	color: #868686;
	line-height: 1.4;
}



