
.address-card-wrap{
	background-color: #fff;
	border-bottom: 8px solid #efeff4;
	padding-left: 8px;
	padding-right: 8px;
}
.address-head{
	height: 40px;
	line-height: 40px;
    overflow: hidden;
}
.address-head .zui-icon{
	float: right;
	display: block;
	padding-left: 4px;
	height: 100%;
	font-size: 18px;
	line-height: 40px;
	color: #868683;
}
.address-head .name{
    font-size: 15px;
    font-weight: 500;
    color: #404040;
    float: left;
    display: block;
}
.address-head .tell{
    font-size: 15px;
    font-weight: 500;
    color: #404040;
    float: right;
    display: block;
}
.address-body{
    display: block;
    font-size: 14px;
    line-height: 1.5;
    color: #404040;
    padding-bottom: 8px;
}
.address-foot{
	border-top: 1px dashed #eee;
	height: 36px;
	line-height: 36px;
}
.address-foot .left{
	float: left;
	width: 40%;
	font-size: 12px;
	color: #404040;
	vertical-align: middle;
}
.address-foot .left.active,
.address-foot .left.active .icon:before{
	color: #ed7a5d;
}
.address-foot .left .icon:before{
	color: #404040;
	font-size: 16px;
	line-height: 2.1;
}
.address-foot .right{
	float: right;
	width: 60%;
	text-align: right;
	vertical-align: middle;
}
.address-foot .right .zui-icon{
	display: inline-block;
	height: 100%;
	padding: 8px 0 8px 8px;
	font-size: 12px;
	color: #404040;
	letter-spacing: 1px;
}



