
.coupon-card-wrap{
	display: block;
	margin-bottom: 10px;
}
.coupon-card-wrap .up{
	border-radius: 6px 6px 0 0;
	padding-left: 14px;
	height: 80px;
	overflow: hidden;
	box-sizing: border-box;
}
.coupon-card-wrap .up .left{
	float: left;
	width: 80%;
	padding-top: 10px;
	padding-bottom: 10px;
	color: #fff;
}
.coupon-card-wrap .up .left .title{
	font-size: 30px;
	line-height: 1.2;
}
.coupon-card-wrap .up .left .subtitle{
	font-size: 16px;
}
.coupon-card-wrap .up .right{
	float: left;
	width: 20%;
	box-sizing: border-box;
	padding-top: 12px;
}
.coupon-card-wrap .up .right img{
	width: 100%;
	display: block;
}
.coupon-card-wrap .down{
	padding-top: 8px;
	padding-bottom: 8px;
	padding-left: 14px;
	font-size: 14px;
	color: #404040;
	border-radius: 0 0 6px 6px;
	background-color: #fff;
}
.coupon-unuse .up{
	background-color: #69c9c3;
}
.coupon-used .up,
.coupon-timeout .up{
	background-color: #cdcdcd;
}
.coupon-used .down,
.coupon-timeout .down{
	color: #b8b4b4;
}



