
.good-list-wrap{
	overflow: hidden;
	padding: 8px;
	border-bottom: 1px solid #f8f8f8;
}
.good-list-wrap:last-child{
	border-bottom: none;
}
.good-list-wrap .good-list-img{
	display: block;
	float: left;
	height: 100px;
	width: 100px;
}
.good-list-wrap .good-list-info{
	overflow: hidden;
	padding-left: 8px;
}
.good-list-wrap .good-list-info .title{
	font-size: 16px;
	line-height: 1.2;
	color: #404040;
	margin-bottom: 8px;
}
.good-list-wrap .good-list-info .subInfo{
	font-size: 12px;
	line-height: 1.3;
	color: #868686;
	margin-bottom: 4px;
}
.good-list-wrap .good-list-info .subInfo .param{
	padding-right: 4px;
}
.good-list-wrap .good-list-info .price{
	font-size: 16px;
	line-height: 1.3;
	color: #404040;
}
.good-list-wrap .good-list-info .price .num{
	float: right;
	font-size: 12px;
	color: #868686;
	line-height: 1.6;
}



